    function redirect(dimension, page) {
        var redirect = "";
        var dimension = typeof dimension == "undefined" || dimension == null ? '' : dimension;
        var page = typeof page == "undefined" || page == null ? '' : page;
        
        redirect = '/sok/reg/kompletta-hjul/dack?dimension=';
        // if(productTypeID == 1) { redirect = '/sok/reg/sommardack?dimension='; }
        // if(productTypeID == 2) { redirect = '/sok/reg/friktionsdack?dimension='; }
        // if(productTypeID == 3) { redirect = '/sok/reg/dubbdack?dimension='; }
        // console.log(dimension, page);
        if(dimension) {
            redirect = redirect + dimension;
        } 

        if(page) {
            redirect = redirect + '&page=' + page;
        }

        if (history.pushState) {
          window.history.pushState("", "", redirect);
        } else {
          document.location.href = redirect;
        }
        return;
    }
    $(document).on('click', '.pagination a', function(e) {
        e.preventDefault();
        var url = $(this).attr('href');
        var page = $(this).text();

        var dimension = $('.dimensions .active').data('dimension');
        var brand = $(".DDBrands").val();
        var textSearch = $('#textSearch').val();
        redirect(dimension, page);


        $.ajax({
            type: 'GET',
            url: url, 
            data: {
                textSearch: textSearch,
                brand: brand,
                productTypeID: productTypeID,
                dimension : dimension
            },
            dataType: 'json',
            xhrFields: {
                withCredentials: true
            },
            success: function(data) {
                $('#searchResult').remove();
                $('.categoryFooter').remove();
                $('#searchContainer div.col-sm-12').append(data.searchResult);

                $('html, body').animate({scrollTop : 0},1000);
            }
        });
    });

